.kanban-container{
    //width: 100% !important;
    display: flex;
    justify-content: start;
    align-items: stretch;
    flex-wrap: wrap;
    flex: 1 1 auto;

    > div{
        background: var(--card-color);
        border: 1px dashed var(--border-color);
        border-radius: .75rem;
        flex: 1 0 !important;
        overflow: hidden;
        margin-right: 10px !important;

        &:last-child{
            margin-right: 0 !important;
        }
    }

    .kanban-drag{
        padding: 15px;
        .kanban-item{
            border: 1px dashed var(--border-color);
            background: var(--body-color);
            border-radius: .75rem;
            margin-bottom: 5px;
        }
    }
}

.tq_jkanban_color{
    [data-order="1"]{
        border-color: var(--theme-color1);
        .kanban-title-board{
            color: var(--theme-color1);
        }
        .kanban-item{
            background: rgba(var(--primary-rgb),.1);
        }
    }
    [data-order="2"]{
        border-color: var(--bs-warning);
        .kanban-title-board{
            color: var(--bs-warning);
        }
        .kanban-item{
            background: rgba(var(--bs-warning-rgb),.1);
        }
    }
    [data-order="3"]{
        border-color: var(--bs-success);
        .kanban-title-board{
            color: var(--bs-success);
        }
        .kanban-item{
            background: rgba(var(--bs-success-rgb),.1);
        }
    }
}
.tq_jkanban_advanced{
    overflow-x: auto;
    padding-bottom: 10px;
    [data-id="todo"]{
        border-color: var(--theme-color1);
        background: var(--body-color);
        .kanban-board-header{
            background: var(--theme-color1);
            color: $white;
        }
        .kanban-item{
            background: var(--card-color);
        }
    }
    [data-id="working"]{
        border-color: var(--theme-color2);
        background: var(--body-color);
        .kanban-board-header{
            background: var(--theme-color2);
            color: $white;
        }
        .kanban-item{
            background: var(--card-color);
        }
    }
    [data-id="done"]{
        border-color: var(--theme-color3);
        background: var(--body-color);
        .kanban-board-header{
            background: var(--theme-color3);
            color: $white;
        }
        .kanban-item{
            background: var(--card-color);
        }
    }
}
