// bootstrap class overwrite css
.border,
.border-top,
.border-right,
.border-end,
.border-bottom,
.border-left,
.border-start,
.dropdown-divider,
.input-group-text {
    border-color: var(--border-color) !important;

    &.border-primary{
        border-color: var(--primary-color) !important;
    }
    &.border-secondary{
        border-color: var(--secondary-color) !important;
    }
    &.border-success{
        border-color: $color-success !important;
    }
    &.border-danger{
        border-color: $color-danger !important;
    }
    &.border-warning{
        border-color: $color-warning !important;
    }
    &.border-info{
        border-color: $color-info !important;
    }
    &.border-dark{
        border-color: var(--gray-9) !important;
    }
    &.border-white{
        border-color: var(--white) !important;
    }
    &.border-muted{
        border-color: var(--gray-4) !important;
    }
    &.dashed{
        border-style: dashed !important;
    }
}


// avatar
.avatar {
    width: 38px;
    min-width: 38px;
    height: 38px;

    &.xs{
        width: 16px;
        min-width: 16px;
        height: 16px;
        font-size: 8px;
    }
    &.sm{
        width: 26px;
        min-width: 26px;
        height: 26px;
    }
    &.lg{
        width: 56px;
        min-width: 56px;
        height: 56px;
    }
    &.xl{
        width: 90px;
        min-width: 90px;
        height: 90px;
    }
    &.xxl{
        width: 120px;
        min-width: 120px;
        height: 120px;
    }
    &.no-thumbnail{
        background-color: var(--primary-color);
        color: var(--white);
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

// dropdown menu
.dropdown{
    &.material-ui{
        .btn{
            position: relative;
            z-index: 10;
        }
        .dropdown-menu[data-popper-placement="top-start"]{
            padding-bottom: 3rem !important;
            bottom: -2.8rem !important;
            left: -0.5rem !important;
            z-index: 9;
        }
        .dropdown-menu[data-popper-placement="bottom-start"]{
            padding-top: 3rem !important;
            top: -2.8rem !important;
            left: -0.5rem !important;
            z-index: 9;
        }
    }
}
.dropdown-menu {
    font-size: $font-size + 1px;
    -webkit-animation-name:DropDownSlide;
    animation-name:DropDownSlide;
    -webkit-animation-duration:.3s;
    animation-duration:.3s;
    -webkit-animation-fill-mode:both;
    animation-fill-mode:both;
    position:absolute;
    z-index:1000;

    &[data-popper-placement=top-start] {
        -webkit-animation-name:DropDownSlideDown;
        animation-name:DropDownSlideDown
    }
    
    .dropdown-item:hover,
    .dropdown-item:focus,
    .dropdown-item.active{
        background: rgba(var(--primary-rgb), 0.1);
        color: var(--primary-color);
    }
    &.mega-dropdown{
        width: 600px;
        
        @media only screen and (max-width: $break-xsmall) {
            width: 100%;
        }
    }
    &.datepicker{
        color: var(--gray-6);
        border: 0;
        padding: 1rem;
        border-radius: 1rem;
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;

        &::before, &::after{
            left: 27px;
        }

        td,
        th{
            width: 32px;
            height: 32px;
        }
        td.day.active{
            background: var(--primary-gradient);
        }
        td.day.focused,
        td.day:hover{
            background: var(--primary-color);
            color: var(--white);
        }
    }

    @-webkit-keyframes DropDownSlide {
        100% {margin-top:-1px}
        0% {margin-top:8px}
    }
    @keyframes DropDownSlide {
            100% {margin-top:-1px}
            0% {margin-top:8px}
    }
    @-webkit-keyframes DropDownSlideDown {
            100% {margin-bottom:0}
            0% {margin-bottom:8px}
    }
    @keyframes DropDownSlideDown {
            100% {margin-bottom:0}
            0% {margin-bottom:8px}
    }
}

.after-none::after{
    display: none;
}

// custom modal popup css
.modal {
    right: 0;
    left: auto;
    
    &.fade{
        .modal-dialog-vertical{
            transform: translateX(-100%);
        }
    }
    &.show{
        .modal-dialog-vertical{
            transform: translateX(0);

            &.right-side{
                right: 0;
            }
        }
    }
    .modal-header,
    .modal-footer{
        border-color: var(--border-color);
    }
    .modal-title{
        color: var(--gray-8);
    }   
    .modal-content{
        background-color: var(--card-color);
        border-color: var(--border-color);
        color: var(--gray-6);
        .dropdown-item{
            color: var(--gray-5);

            &:hover{
                color: var(--primary-color);
                background-color: rgba(var(--primary-rgb), 0.1);
            }
        }
    }
    .modal-dialog-vertical{
        margin: 0;
        height: 100%;
        
        .modal-content{
            height: 100%;
            border: 0;
            border-radius: 0;
        }
        &.right-side{
            @include transition(ease-in-out .2s);
            transform: none;
            position: absolute;
            right: -300px;
            @media only screen and (max-width: $break-xsmall) {
                position: unset;
            }
        }
    }
}

// custom offcanvas popup css
.offcanvas {
    background-color: var(--card-color);

    &.sm{
        @media only screen and (min-width: $break-xlarge) {
            width: 360px;
            padding: 1rem;
        }
    }

    &.xl{
        @media only screen and (min-width: $break-xlarge) {
            width: 460px;
            padding: 1rem;
        }
    }
    &.xxl{
        @media only screen and (min-width: $break-large) {
            width: 700px;
            padding: 1rem;
        }
    }

    .offcanvas-header,
    .offcanvas-footer{
        border-color: var(--border-color);
    }
    .offcanvas-title{
        color: var(--gray-8);
    }
    .offcanvas-content{
        background-color: var(--card-color);
        color: var(--gray-6);
        .dropdown-item{
            color: var(--gray-5);

            &:hover{
                color: var(--primary-color);
                background-color: rgba(var(--primary-rgb), 0.1);
            }
        }
    }
}

// breadcrumb
.breadcrumb {
    background-color: var(--gray-2);
}

// navbar  
.navbar-light {
    .navbar-nav {
        .nav-link {
            color: var(--gray-5);
            &:hover,
            &:focus{
                color: var(--primary-color);
            }
        }
    }
}

// custom tab ui
.nav-tabs {
    border-color: var(--border-color);
    
    &.tab-heading{
        border: 0;
        .nav-item{
            margin: 0 3px;
        }
        .nav-link{
            border-radius: .3rem;
            color: var(--gray-5);
            border-color: var(--card-color);
            &:hover,
            &.active{
                background: var(--primary-color);
                color: var(--white);
                box-shadow: 0px 7px 29px 0px rgba(var(--primary-rgb), 0.4);
            }
        }
    }
    &.tab-card{
        padding: 0 1.5rem;

        @media only screen and (max-width: $break-small) {
            padding: 0 1rem;
        }
        .nav-link{
            color: var(--gray-6);
            background-color: transparent;
            border-bottom: 2px solid transparent;
            border-top: 0;
            border-left: 0;
            border-right: 0;
            margin-bottom: 0;

            span{
                color: var(--gray-8);
                font-weight: 600;
            }

            @media only screen and (max-width: $break-small) {
                padding-left: 10px;
                padding-right: 10px;
            }

            &.active,
            &:hover{
                border-color: var(--primary-color);
                color: var(--primary-color);
                span{
                    color: var(--primary-color);
                }
            }
        }
    }

    &.tab-page-toolbar{
        border: 1px solid Var(--primary-color);
        overflow: hidden;
        padding: 3px;
        .nav-link{
            border: 0;
            margin: 0;
            padding: .3rem 1rem;
            border-radius: .25rem;
            color: var(--gray-5);

            &:hover{
                color: var(--primary-color);
            }

            &.active{
                background-color: var(--primary-color);
                color: $white;
            }
        }
    }
    &.menu-link{
        flex-direction: column;
        .nav-link{
            color: var(--gray-5);
            display: flex;
            align-items: center;
            border-radius: .25rem;
            margin: 0;
            border: 0;
            padding: .6rem 0;

            &.active,
            &:hover{
                color: var(--primary-color);
                background-color: transparent;
                font-weight: bold;
            }

            i.fa{
                width: 26px;
            }
        }
    }
}

// nav pill
.nav-pills {
    .nav-link.active,
    .show>.nav-link{
        background-color: var(--primary-color);
    }
    .nav-link{
        color: var(--gray-5);
    }
    &.custom-pill{
        margin-left: -.25rem;
        .nav-link {
            position: relative;
            &.active,
            &:hover{
                background-color: var(--primary-color);
                &:after {
                    color: var(--primary-color);
                    content: '\f0d7';
                    font-family: 'FontAwesome';
                    position: absolute;
                    bottom: -18px;
                    left: 10px;
                    font-size: 20px;
                }
            }
        }
    }
    &.custom-pill2{
        margin-left: -.25rem;
        .nav-link {
            position: relative;
            &.active,
            &:hover{
                color: var(--primary-color);
                background-color: transparent;
                &:after {
                    color: var(--primary-color);
                    content: "\f111";
                    font-family: 'FontAwesome';
                    position: absolute;
                    bottom: -15px;
                    left: 50%;
                    font-size: 12px;
                    transform: translate(-50%);
                    border: 5px solid var(--card-color);
                }
            }
        }
    }
    &.custom-horizontal{
        border-right: 1px solid var(--primary-color);
        flex-direction: column;
        text-align: right;
        .nav-link{
            border-right: 4px solid transparent;
            color: var(--gray-7);
            border-radius: 0;
            &.active {
                color: var(--primary-color);
                border-color: var(--primary-color);
                background-color: transparent;
            }
        }
    }
}

// list-group design
.list-group-custom {
    .list-group-item{
        background-color: var(--card-color);
        border-color: var(--border-color);
        color: var(--gray-7);
        border-style: dashed;
        padding: .65rem 1rem;

        &:hover,
        &.active{
            background-color: rgba(var(--primary-rgb), 0.1);
        }
    }
    &.sticky-top {
        z-index: 8;
    }
}
.list-group-item {
    background-color: var(--card-color);
    border-color: var(--border-color);
}

// progress bar design
.progress {
    background-color: var(--gray-2);
}

//alert
.alert{
    border-radius: 0.75rem;
    border-style: dashed;
}

// table 
.table {
    border-color: var(--border-color);
    tr{
        th{
            //border-color: var(--primary-color);
            color: var(--gray-8);
            text-transform: uppercase;
            font-size: 12px;
        }
        td{
            border-color: var(--border-color);
            color: var(--gray-6);
            padding: .8rem .6rem;
        }
        &:hover{
            td{
                color: var(--gray-9);
            }
        }
    }
    &.table-striped{
        >tbody>tr:nth-of-type(odd)>*{
            color: var(--gray-7);
        }
    }
    &.border-primary{
        tr th,
        tr td{
            border-color: var(--primary-color);
        }
    }
    &.table-sm{
        td{
            padding: .5rem .6rem;
        }
    }
    &.custom-table{
        border-collapse: separate !important;
        border-spacing: 0 5px !important;

        thead{
            tr{
                th{
                    border: 0;
                }
            }
        }
        tbody{
            tr{
                background: var(--card-color);
            }
            &:hover{
                tr{
                    @include transition(ease .3s);
                    opacity: .7;
                    &:hover{
                        opacity: 1;
                    }
                }
            }    
        }
        tr{
            td, th{
                vertical-align: middle;
                white-space: nowrap;
                padding-left: 1rem;
                padding-right: 1rem;
                border: 0;
                font-size: 14px;
                
                &:first-child{
                    border-radius: 0.25rem 0 0 0.25rem;
                    border-left: 0;
                }
                &:last-child{
                    border-radius: 0 0.25rem 0.25rem 0;
                }
            }
        }
    }
    &.custom-table-2{
        border-collapse: separate !important;
        border-spacing: 0 1px !important;

        thead{
            tr{
                th{
                    border: 0;
                }
            }
        }
        tbody{
            tr{
                background: var(--card-color);
                &.cancle-row{
                     td{
                        background: rgba($color-danger, .05);
                    }
                }
                &.active-row{
                     td{
                        background: rgba($color-info, .05);
                    }
                }
            }    
        }
        tr{
            td, th{
                vertical-align: middle;
                white-space: nowrap;
                padding-left: 1rem;
                padding-right: 1rem;
                border-right: 0;
                
                &:first-child{
                    border-radius: 0.25rem 0 0 0.25rem;
                    border-left: 0;
                }
                &:last-child{
                    border-radius: 0 0.25rem 0.25rem 0;
                }
            }
        }
    }
    &.card-table{

        thead{
            tr th{
                border-top: 1px dashed var(--border-color);
            }
        }
                
        tbody{
            border-top: none;
            tr{
                background: var(--card-color);
            }
            &:hover{
                tr{
                    @include transition(ease .3s);
                    opacity: .7;
                    &:hover{
                        background: var(--body-color);
                        opacity: 1;
                    }
                }
            }    
        }
        tr{
            td, th{
                vertical-align: middle;
                white-space: nowrap;
                padding-left: 1rem;
                padding-right: 1rem;
                border-right: 0;
                border-bottom: 1px dashed var(--border-color);
                border-left: 1px dashed var(--border-color);
                
                &:first-child{
                    border-radius: 0.25rem 0 0 0.25rem;
                    border-left: 0;
                }
                &:last-child{
                    border-radius: 0 0.25rem 0.25rem 0;
                }
            }
            &:last-child{
                td{
                    border-bottom: 0;
                }
            }
        }
    }
}

// pagination
.page-item{

    .page-link{
        background-color: var(--card-color);
        border-color: var(--border-color);
        color: var(--gray-6);
    }
    
    &.disabled{
        .page-link{
            background-color: var(--border-color);
            border-color: var(--border-color);
        }
    }
    &.active,
    &:hover{
        .page-link{
            background-color: var(--primary-color);
            border-color: var(--border-color);
            color: var(--white);
        }
    }
}

.fc-custome{
    .btn{
        border: 1px dashed var(--border-color);
        &.active{
            border: 1px dashed var(--primary-color);
            background: rgba(var(--primary-rgb), 0.1);
            color: var(--primary-color);
        }
    }
}

// tooltip
.tooltip {
    &.bs-tooltip-top .tooltip-arrow::before, .bs-tooltip-auto[data-popper-placement^=top] .tooltip-arrow::before{
        border-top-color: var(--primary-color);
    }
    &.bs-tooltip-end .tooltip-arrow::before, .bs-tooltip-auto[data-popper-placement^=right] .tooltip-arrow::before{
        border-right-color: var(--primary-color);
    }
    &.bs-tooltip-bottom .tooltip-arrow::before, .bs-tooltip-auto[data-popper-placement^=bottom] .tooltip-arrow::before{
        border-bottom-color: var(--primary-color);
    }
    &.bs-tooltip-start .tooltip-arrow::before, .bs-tooltip-auto[data-popper-placement^=left] .tooltip-arrow::before{
        border-left-color: var(--primary-color);
    }
    
    .tooltip-inner{
        background-color: var(--primary-color);
        color: $white;
    }
}

// badge
.badge{
    &.bg-warning{
        color: $dark;
    }
}

.accordion{
    .accordion-item{
        border-color: var(--border-color);
    }
    .accordion-button:not(.collapsed){
        background: rgba(var(--primary-rgb), 0.1);
        color: var(--primary-color);
        box-shadow: none;
    }
}


.py-6{
    padding-top: 4rem !important; padding-bottom: 4rem !important;
    @media only screen and (max-width: $break-1280) {
        padding-top: 3rem !important; padding-bottom: 3rem !important;
    }
    @media only screen and (max-width: $break-large) {
        padding-top: 2.5rem !important; padding-bottom: 2.5rem !important;
    }
    @media only screen and (max-width: $break-medium) {
        padding-top: 1.5rem !important; padding-bottom: 1.5rem !important;
    }
}
.py-7{ padding-top: 5rem !important; padding-bottom: 5rem !important;}

.rounded-4{
    border-radius: .75rem!important;
}
.rounded-5{
    border-radius: .8rem!important;
}

// .offcanvas
.offcanvas{
    @media only screen and (min-width: $break-xlarge) {
        &.lg{
            width: 500px;
        }
    }
}