// tagify
.tagify{
    border-color: var(--border-color);
    border-radius: 5px;
}
.tags-look {
    .tagify__dropdown__item{
        display: inline-block;
        border-radius: 3px;
        padding: .3em .5em;
        border: 1px solid var(--border-color);
        background: var(--border-color);
        margin: .2em;
        font-size: .85em;
        color: var(--gray-8);
        transition: 0s;
    }
    .tagify__dropdown__item--active{
        color: var(--gray-8);
    }
    .tagify__dropdown__item:hover{
        background: lightyellow;
        border-color: gold;
    }
}

.customLook {
    --tag-bg                  : var(--primary-color);
    --tag-hover               : var(--accent-color);
    --tag-text-color          : var(--white);
    --tags-border-color       : var(--gray-3);
    --tag-text-color--edit    : var(--gray-8);
    --tag-remove-bg           : var(--tag-hover);
    --tag-pad                 : .6em 1em;
    --tag-inset-shadow-size   : 1.35em;
    --tag-remove-btn-bg--hover: var(--black);
  
    display: inline-block;
    border: none;
    min-width: 0;

    .tagify__tag{
        margin-top: 0;
        > div{
            border-radius: 25px;
        }

        /* Do not show the "remove tag" (x) button when only a single tag remains */
        &:only-of-type .tagify__tag__removeBtn{
            display: none;
        }
    }
    
    .tagify__tag__removeBtn{
        opacity: 0;
        transform: translateX(-6px) scale(.5);
        margin-left: -3ch;
        transition: .12s;
    }
    
    .tagify__tag:hover .tagify__tag__removeBtn{
        transform: none;
        opacity: 1;
        margin-left: -1ch;
    }
    
    + button{
        color: var(--primary-color);
        font: bold 1.4em/1.65 Arial;
        background: none;
        box-shadow: 0 0 0 2px inset currentColor;
        cursor: pointer;
        outline: none;
        transition: .1s ease-out;
        vertical-align: top;
        border: 0;
        border-radius: 50%;
        width: 1.65em;
        height: 1.65em;
        margin: 0 0 0 5px;

        &:hover{
            box-shadow: 0 0 0 5px inset currentColor;
        }
    }
    
    .tagify__input{
        display: none;
    }
}

/* Suggestions items */
.tagify__dropdown{
    &.users-list {
        .tagify__dropdown__item{
            padding: .5em .7em;
            display: grid;
            grid-template-columns: auto 1fr;
            gap: 0 1em;
            grid-template-areas: "avatar name" "avatar email";
            &:hover .tagify__dropdown__item__avatar-wrap{
                transform: scale(1.2);
            }
        }
        .tagify__dropdown__item__avatar-wrap{
            grid-area: avatar;
            width: 36px;
            height: 36px;
            border-radius: 50%;
            overflow: hidden;
            background: var(--border-color);
            transition: .1s ease-out;
        }
        img{
            width: 100%;
            vertical-align: top;
        }
        strong{
            grid-area: name;
            width: 100%;
            align-self: center;
        }
        span{
            grid-area: email;
            width: 100%;
            font-size: .9em;
            opacity: .6;
        }
        .tagify__dropdown__item__addAll{
            border-bottom: 1px solid var(--border-color);
            gap: 0;
        }
    }
}

/* Tags items */
#users-list {
    .tagify__tag{
        white-space: nowrap;
        &:hover .tagify__tag__avatar-wrap{
            transform: scale(1.6) translateX(-10%);
        }
        .tagify__tag__avatar-wrap{
            width: 16px;
            height: 16px;
            white-space: normal;
            border-radius: 50%;
            background: var(--gray-3);
            margin-right: 5px;
            transition: .12s ease-out;
        }
        img{
            width: 100%;
            vertical-align: top;
            pointer-events: none;
        }
    }
}

.tagify__dropdown.extra-properties .tagify__dropdown__item > img{
    display: inline-block;
    vertical-align: middle;
    height: 20px;
    transform: scale(.75);
    margin-right: 5px;
    border-radius: 2px;
    transition: .12s ease-out;
}

.tagify__dropdown.extra-properties .tagify__dropdown__item--active > img,
.tagify__dropdown.extra-properties .tagify__dropdown__item:hover > img{
    transform: none;
    margin-right: 12px;
}

.tagify{
    &.countries {
        .tagify__input{
            min-width:175px;
        }
        tag{ white-space:nowrap; }
        tag img{
            display: inline-block;
            height: 16px;
            margin-right: 3px;
            border-radius: 2px;
            pointer-events: none;
        }
    }
}