// form-control
.form-floating{
    >label{
        font-size: 15px;    
    }
}
.form-control,
.form-select {
    border-color: var(--border-color);
    background-color: var(--card-color);
    color: var(--color-900);
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: 15px;

    &::placeholder{
        color: var(--gray-4);
    }
    &:hover{
        border-color: rgba(var(--primary-rgb), .3);
    }
    &:focus{
        box-shadow: 0 0 10px rgba($dark, .15);
        background-color: var(--card-color);
        border-color: var(--primary-color);
        color: var(--primary-color);
    }

    &.form-control-lg{
        min-height: calc(1.5em + 1.8rem + 2px);
    }
    &:disabled {
        cursor: not-allowed;
    }
}

// form-control with Icon
.form-icon-group {
    display: block;
    .form-icon {
        top: 50%;
        transform: translateY(-50%);
        padding: .375rem .50rem;
        width: 35px;
        height: 35px;
        z-index: 3;
    }
    svg {
        fill: var(--gray-6);
    }
    &.left-icon {
        .form-icon {
            left: 0;
            right: auto;
        }
        .form-control {
            padding-left: 2.05rem;
        }
    }
    &.right-icon {
        .form-icon {
            left: auto;
            right: 0;
        }
        .form-control {
            padding-right: 2.05rem;
        }
    }
    
}

// form Switches css
.form-check-input {
    background-color: var(--border-color);
    border-color: var(--border-color);
    -webkit-appearance: none;
    &:checked {
        background-color: var(--primary-color);
        border-color: var(--primary-color);
    }
}

// custome radio btn
.c_radio{

    > label {
        cursor: pointer;

        input[type="radio"],
        input[type="checkbox"] {
            position: absolute;
            opacity: 0;

            &:checked + .card {
                @include transition(ease-in .1s);
                border: 1px dashed var(--primary-color);
                background: rgba(var(--primary-rgb),.15);

                &:after {
                    background: var(--primary-color);
                    border: 3px solid var(--card-color);
                    box-shadow: 0px 0px 0px 2px var(--primary-color);
                    //content: "";
                    position: absolute;
                    height: 1rem;
                    width: 1rem;
                    right: 20px;
                    top: 20px;
                }
            }
        }

        input[type="radio"] {
            &:checked + .card {
                &:after {
                    border-radius: 100%;
                }
            }
        }

        .card {
            &:hover {
                border: 1px dashed var(--primary-color);
                background: rgba(var(--primary-rgb),.1);
            }
        }
    }
}

// img upload 
.file-input{
    input{
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
    }
    label:hover{
        cursor: pointer;
        color: var(--primary-color);
    }
}
.image-input{
    position: relative;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: cover;

    .avatar-wrapper{
        background-repeat: no-repeat;
        background-size: cover !important;
        width: 100%;
        height: 100%;
    }

    &.avatar{
        &.xxl{
            .file-input{
                position: absolute;
                bottom: 10px;
                right: 10px;
            }
        }
        &.xl{
            .file-input{
                position: absolute;
                bottom: 5px;
                right: 5px;
            }
        }
        &.lg{
            .file-input{
                position: absolute;
                bottom: -5px;
                right: -5px;
            }
        }
    }

    .file-input{
        label{
            background: var(--card-color);
            text-align: center;
            height: 24px;
            width: 24px;
            line-height: 24px;
            border-radius: 24px;
        }
    }
}