.inbox{
    .page-title{
        padding: 1rem 0;
    }
    .breadcrumb{
        margin-bottom: 0;
    }
    .user-list{
        a{
            color: var(--gray-7);
            display: flex;
            text-decoration: none;
        }
        .status{
            font-size: 8px;
        }
        .time{
            font-size: 10px;
        }
    }
    .action-body{
        height: calc(100vh - 440px);
        @media only screen and (max-width: $break-large) {
            height: calc(100vh - 200px);
        }
    }
}