// Landingpage-one
.landing{
    .scroll-top {
        width: 45px;
        height: 45px;
        line-height: 45px;
        background: var(--primary-color);
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        font-size: 14px;
        color: var(--white-color) !important;
        position: fixed;
        bottom: 30px;
        right: 30px;
        z-index: 9;
        cursor: pointer;
        -webkit-transition: all .3s ease-out 0s;
        transition: all .3s ease-out 0s;
        border-radius: 5px;

        &:hover {
            -webkit-box-shadow: 0 1rem 3rem rgba(35, 38, 45, 0.15) !important;
            box-shadow: 0 1rem 3rem rgba(35, 38, 45, 0.15) !important;
            -webkit-transform: translate3d(0, -5px, 0);
            transform: translate3d(0, -5px, 0);
            background-color: var(--primary-color);
        }
    }

    .header {
        width: 100%;
        background: transparent;
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;

        .navbar-brand {
            img {
                width: 200px;
            }
        }

        .navbar {
            .navbar-nav {
                @media only screen and(min-width: 991px) {
                    align-items: center;
                }
                .nav-item {
                    a.active {
                        color: var(--white-color);
                    }

                    .sub-menu {
                        a.active {
                            color: var(--white-color);
                        }
                    }
                }
            }
            .navbar-collapse{
                @media only screen and (min-width: 991px) and (max-width: 1199px) {
                    background-color:var(--primary-color);
                }
            }
        }

        .button {
            display: inline-block;
            margin-left: 10px;

            .btn {
                background-color: var(--white-color);
                color: var(--primary-color);
                font-weight: 500;

                &:hover {
                    color: var(--white-color);
                    background-color: var(--primary-color);
                }
            }
        }

        &.sticky{
            .button {
                .btn {
                    background-color: var(--primary-color);
                    color: var(--white-color);
    
                    &:hover {
                        background-color: var(--primary-color);
                        color: var(--white-color);
                    }
                }
            }
        }
    }

    .sticky {
        position: fixed;
        z-index: 99;
        -webkit-box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
        box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
        -webkit-transition: all 0.3s ease-out 0s;
        transition: all 0.3s ease-out 0s;
        top: 0;
        background-color: var(--white-color);

        .navbar {
            .navbar-nav {
                .nav-item {
                    a.active {
                        color: var(--primary-color);
                    }

                    a {
                        color: var(--primary-color);
                    }

                    .sub-menu {
                        a.active {
                            color: var(--white-color);
                        }
                    }
                    a.btn{
                        background-color: var(--primary-color) !important;
                        color: var(--white-color) !important;
                    }
                }
            }

            .mobile-menu-btn {
                .toggler-icon {
                    background: #333;
                }
            }

            .navbar-brand {
                color: var(--primary-color);
            }
            .navbar-collapse{
                @media only screen and (min-width: 991px) and (max-width: 1199px) {
                    background-color:var(--white-color);
                }
            }
        }

        .navbar-nav {
            .nav-item {
                &:hover {
                    a {
                        color: var(--theme-color5);
                    }
                }
            }
        }

        .button {
            .btn {
                background-color: var(--primary-color);
                color: var(--white-color);

                &:hover {
                    background-color: var(--primary-color);
                    color: var(--white-color);
                }
            }
        }
    }

    .navbar-area {
        width: 100%;
        z-index: 99;
        -webkit-transition: all 0.3s ease-out 0s;
        transition: all 0.3s ease-out 0s;
        padding: 0;
        &.sticky {
            position: fixed;
            z-index: 99;
            -webkit-box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
            box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.05);
            -webkit-transition: all 0.3s ease-out 0s;
            transition: all 0.3s ease-out 0s;
            padding: 0px 0;
        }
    }

    .hero-area {
        position: relative;
        background-color: var(--primary-color);
        padding: 180px 0 180px 0;
        z-index: 0;

        .hero-shape {
            position: absolute;
            left: 0;
            bottom: -1px;
            z-index: -1;
        }

        .hero-content {
            border-radius: 0;
            position: relative;
            z-index: 1;
            text-align: left;

            h4 {
                color: var(--white-color);
                font-weight: 600;
                font-size: 15px;
                margin-bottom: 20px;
            }

            h1 {
                font-weight: 700;
                font-size: 38px;
                line-height: 60px;
                color: var(--white-color);
                text-transform: capitalize;

                span {
                    position: relative;
                    z-index: 0;

                    .text-shape {
                        position: absolute;
                        left: 0;
                        bottom: 5px;
                        width: 100%;
                        z-index: -1;
                    }
                }
            }

            p {
                font-weight: 400;
                font-size: 16px;
                line-height: 28px;
                color: var(--white-color);
                margin-top: 20px;
            }

            .button {
                margin-top: 50px;

                .btn {
                    &:hover {
                        color: var(--primary-color);
                        background-color: var(--white-color);
                    }
                }
            }
        }

        .hero-image {
            text-align: center;
            position: relative;
            z-index: 0;

            .main-image {
                z-index: 0;
            }

            .h2-move-1 {
                position: absolute;
                left: 42%;
                -webkit-animation: bounce 3s linear infinite;
                animation: bounce 3s linear infinite;
            }

            .h2-move-2 {
                position: absolute;
                left: 45%;
                top: 68%;
                -webkit-animation: bounce 3s linear infinite;
                animation: bounce 3s linear infinite;
            }

            .h2-move-3 {
                position: absolute;
                left: 47%;
                top: 26%;
                z-index: -1;
                -webkit-animation: bounce 4s linear infinite;
                animation: bounce 4s linear infinite;
            }

            .dot-dot {
                position: absolute;
                left: -10%;
                top: -14%;
                height: 113%;
                -webkit-animation: rotate2d 30s linear infinite;
                animation: rotate2d 30s linear infinite;
                width: 100%;
            }
        }
     
    }
}