.chat-app{
    .page-title{
        padding: 1rem 0;
    }
    .breadcrumb{
        margin-bottom: 0;
    }

    .order-1{
        .c-list{
            padding: 14px;
        }
        .list-group-item {
            max-width: 100%;
            border: 0;
            > a{
                text-decoration: none;
                padding: .5rem 0;
            }
        }
        h6{
            color: var(--gray-7);
            font-weight: normal;
        }
        span.text-muted{
            font-size: 13px;
        }
        .msg-time{
            color: var(--gray-4);
            font-size: 11px;
        }
        @media only screen and (min-width: $break-1280) and (max-width: $break-xxlarge) {
            min-width: 300px;
            max-width: 300px;
        }
    }
    .order-2{
        .chat-history{
            position: relative;
            height: calc(100vh - 220px);

            .max-width-70{
                max-width: 70%;
        
                @media only screen and (max-width: $break-small) {
                    max-width: 100%;
                }
            }
            #chat-search{
                position: sticky;
                top: 0;
                left: 0;
                width: 100%;
                z-index: 9;
            }
        }
        .chat-msg{
            border-top: 1px dashed var(--border-color);
            display: flex;
            align-items: center;
            
            > .form-control:focus{
                border: 0;
                box-shadow: none;
            }
        }
    }
    
    .action-body{
        height: calc(100vh - 370px);
        @media only screen and (max-width: $break-large) {
            height: calc(100vh - 200px);
        }
    }
}