// apex chart line and text color
.apexcharts-canvas{
    line,
    circle{
		stroke: var(--border-color);
	}
	text{
	//	fill: var(--gray-5);
	}
	.apexcharts-legend-text{
		color: var(--gray-5) !important;
    }
    .apexcharts-pie{
        path{
           stroke: var(--border-color);
        }
    }
    .apexcharts-legend-marker{
        border-radius: 20px !important;
    }
}
.ac-line-transparent{
    line{
        stroke: var(--card-color);
    }
}
.ac-line-primary{
    line{
        stroke: var(--primary-color);
    }
}
.apex-extra-none{
    line{
        display: none;
    }
}
.apexcharts-line-0{
    line{
        display: none;
    }
}
.sparkline_chart{
    canvas{
        width: 100% !important;
    }
}