// fancybox plugin css overwrite
.fancybox-overlay{ 
    background-image: url('../vendor/fancybox/source/fancybox_overlay.png');
}
#fancybox-loading,
.fancybox-close,
.fancybox-prev span,
.fancybox-next span{
    background-image: url('../vendor/fancybox/source/fancybox_sprite.png');
}

.gallery_product{
    position:relative;

    .img-info{
        position: absolute;
        overflow:hidden;
        display:none;
        background: rgba(0,0,0,0.5);
        color:#fff;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 20px;
        top:0;
        
        -webkit-transition: 2s;
        transition: 2s;
    }
    &:hover {
        .img-info{
            display:block;
            -webkit-transition: 2s;
            transition: 2s;
        }
    }
}