//@import "../../../../node_modules/chartist/dist/scss/chartist.scss";

.ct-chart{
    height: 300px;

    .ct-label{
        color: var(--gray-6);
    }
    .ct-grids{
        line{
            stroke: var(--border-color);
        }
    }
    .ct-line{
        stroke-width: 2px;
    }
    .ct-point{
        stroke-width: 6px;
    }

    .ct-series{
        &.ct-series-a{
            path,
            line{
                stroke: var(--theme-color1);
            }
        }
        &.ct-series-b{
            path,
            line{
                stroke: var(--theme-color2);
            }
        }
        &.ct-series-c{
            path,
            line{
                stroke: var(--theme-color3);
            }
        }
        &.ct-series-d{
            path,
            line{
                stroke: var(--theme-color4);
            }
        }
    }
    .ct-chart-pie{
        .ct-series{
            &.ct-series-a{
                path,
                line{
                    stroke: var(--card-color);
                    fill: var(--theme-color1);
                }
            }
            &.ct-series-b{
                path,
                line{
                    stroke: var(--card-color);
                    fill: var(--theme-color2);
                }
            }
            &.ct-series-c{
                path,
                line{
                    stroke: var(--card-color);
                    fill: var(--theme-color3);
                }
            }
            &.ct-series-d{
                path,
                line{
                    stroke: var(--card-color);
                    fill: var(--theme-color4);
                }
            }
        }
    }
    .ct-chart-donut{
        .ct-series{
            &.ct-series-a{
                path{
                    fill: var(--theme-color1);
                }
            }
            &.ct-series-b{
                path{
                    fill: var(--theme-color2);
                }
            }
            &.ct-series-c{
                path{
                    fill: var(--theme-color3);
                }
            }
            &.ct-series-d{
                path{
                    fill: var(--theme-color4);
                }
            }
        }
    }
}