.btn-close{
    background-color: $white;
}
// bottom
.btn {
    font-size: $font-size;

    &.btn-primary {
        background-color: var(--primary-color);
        border-color: var(--primary-color);
        
        &:hover{
            background: linear-gradient(to top, rgba(0,0,0, 0.25), rgba(0,0,0, 0.25)) var(--primary-color);
        }
    }

    &.btn-light-primary {
        background-color: rgba(var(--primary-rgb), 0.15);
        color: var(--primary-color);
        border-color: transparent;

        &:hover{
            background-color: var(--primary-color);
            color: $white;
        }
    }
    &.btn-light-secondary{
        background-color: rgba($color-secondary, .15);
        color: $color-secondary;
        border-color: transparent;

        &:hover{
            background-color: $color-secondary;
            color: $white;
        }
    }
    &.btn-light-success{
        background-color: rgba($color-success, .15);
        color: $color-success;
        border-color: transparent;

        &:hover{
            background-color: $color-success;
            color: $white;
        }
    }
    &.btn-light-danger{
        background-color: rgba($color-danger, .15);
        color: $color-danger;
        border-color: transparent;

        &:hover{
            background-color: $color-danger;
            color: $white;
        }
    }
    &.btn-light-warning{
        background-color: rgba($color-warning, .15);
        color: $color-warning;
        border-color: transparent;

        &:hover{
            background-color: $color-warning;
            color: $white;
        }
    }
    &.btn-light-info{
        background-color: rgba($color-info, .15);
        color: $color-info;
        border-color: transparent;

        &:hover{
            background-color: $color-info;
            color: $white;
        }
    }

    &.btn-outline-primary {
        color: var(--primary-color);
        border-color: var(--primary-color);

        &:hover{
            background-color: var(--primary-color);
            color: $white;
        }
    }
    &.btn-outline-secondary {
        border-color: var(--border-color)
    }
    &.btn-outline-success{
        color: $color-success;
        border-color: $color-success;
        &:hover{
            background-color: $color-success;
            color: $white;
        }
    }
    &.btn-outline-danger{
        color: $color-danger;
        border-color: $color-danger;
        &:hover{
            background-color: $color-danger;
            color: $white;
        }
    }
    &.btn-outline-info{
        color: $color-info;
        border-color: $color-info;
        &:hover{
            background-color: $color-info;
            color: $white;
        }
    }

    &.btn-accent{
        background-color: var(--accent-color);
        border-color: var(--accent-color);
        color: var(--white);
    }
    &.btn-white {
        background-color: var(--white);
        border-color: var(--white);
        color: var(--gray-7);
    }
    &.btn-dark {
        background-color: var(--gray-9);
        border-color: var(--gray-9);
        color: var(--gray-4);
    }

    &.btn-lg {
        padding: .89rem 1rem;
        font-size: 1rem;
    }
}
.btn-group{
    .btn-check:checked+.btn-outline-primary,
    .btn-check:active+.btn-outline-primary,
    .btn-outline-primary:active,
    .btn-outline-primary.active,
    .btn-outline-primary.dropdown-toggle.show{
        background-color: var(--primary-color);
        border-color: var(--primary-color);
    }
}
button {
    &:focus {
		&:not(:focus-visible) {
			box-shadow: none;
		}
	}
}
.accordion-button{
    background-color: var(--card-color);
    color: var(--gray-6);
}