// horizontal wizard
.step-app {

    // horizontal wizard
    &.horizontal-wizard{
        > .step-steps {
            display: flex;
            flex-wrap: wrap;
            border-radius: 0;
            
            > li {
                background-color: transparent;
                display: flex;
                align-items: center;
                flex: inherit;
                outline: none;
                font-size: 18px;
                margin-right: 25px;
                flex-grow: 0;
                
                &.active {
                    background-color: transparent;
                    color: var(--primary-color);
                    span {
                        background-color: var(--primary-color);
                    }
                }
                &.done {
                    background-color: transparent;
                    color: var(--bs-green);
                    span {
                        background-color: var(--bs-green);
                    }
                }
                &:hover {
                    background-color: transparent;
                }
                span {
                    background-color: var(--gray-4);
                    color: var(--white);
                    padding: 0 12px;
                    border-radius: .25rem;
                    margin: 0 10px 0 0;
                }
            }
        }
        > .step-content {
            border: 0;
            padding: 0;
        }
        > .step-footer {
            justify-content: flex-end;
            margin-bottom: 0;
    
            .btn {
                margin-left: 5px;
            }
    
            > .step-btn {
                background-color: var(--primary-color);
                color: var(--white);
                padding: .375rem .75rem;
                border-radius: .25rem;
            }
        }
    }

    // Vertical wizard
    &.vertical-wizard{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction: row;
        flex-direction: row;

        >.step-steps {
            flex-direction: column;
            min-width: 220px;
            border-radius: 0;
    
            li {
                flex: 0;
                margin-bottom: 5px;
                border: 0;
    
                &.done,
                &.active{
                    background-color: var(--primary-color);
                }
            }
        }
        >.step-content {
            border: 1px solid var(--primary-color);
            padding: 1.5rem;
            width: 100%;
        }
        > .step-footer {
            justify-content: flex-end;
        }
    }
}