.irs--flat{
    .irs-from,
    .irs-to,
    .irs-single{
        background-color: var(--primary-color);
        &:before{
            border-top-color: var(--primary-color);
        }
    }

    .irs-bar{
        background-color: var(--primary-color);
    }
    .irs-line{
        background-color: var(--body-color);
    }
    .irs-min,
    .irs-max{
        color: var(--gray-5);
        background-color: var(--body-color);
    }
    .irs-handle {
        > i:first-child{
            background-color: var(--primary-color);
        }
    }
}