.calendar-tab{

    .ct-header{
        display: flex;
        align-items: center;
        padding: 1rem;
    }

    > ul{
        @include transition(ease .2s);
        flex-wrap: nowrap;
        overflow-x: auto;
        overflow-y: hidden;
        scroll-behavior: smooth;
        -ms-overflow-style: none;  // IE 10+
        scrollbar-width: none; // Firefox
        padding: 1rem;
        border-radius: 1rem;

        li{
            flex: 1 1 auto;

            &:nth-child(6),
            &:nth-child(12) {
                border-right: 1px solid var(--border-color);
            }
        }

        &::-webkit-scrollbar {
            background: var(--card-color);
            visibility: visible;
            height: 4px;
        }

        &:hover{
            &::-webkit-scrollbar-thumb {
                background: var(--primary-color);
            }    
        }
        
        // Track /
        &::-webkit-scrollbar-track {
            visibility: visible;
            background: transparent;
        }    
        // Handle /
        &::-webkit-scrollbar-thumb {
            background: transparent;
            visibility: visible;
        }
    }
    .nav-link{
        padding: 10px 8px;
        font-size: 1rem;
        color: var(--gray-8);
        span{
            color: var(--gray-5);
            display: flex;
            justify-content: center;
            font-size: 10px;
        }
        &:hover,
        &.active{
            background: var(--primary-color);
            box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
            span{
                color: var(--white);
            }
        }
    }
    @media only screen and (min-width: $break-large) {
        .nav-item{
            margin: 0 5px;
        }   
    }
}