body {
  -moz-transition: ease 0.1s;
  -o-transition: ease 0.1s;
  -webkit-transition: ease 0.1s;
  transition: ease 0.1s;
  color: var(--gray-5);
  font-family: "Exo", sans-serif;
  direction: ltr;
  font-size: 16px;
}
body.radius-0 .card,
body.radius-0 .card .card-header,
body.radius-0 .card .card-footer,
body.radius-0 .sidebar .menu-link,
body.radius-0 .form-select,
body.radius-0 .btn,
body.radius-0 .form-control,
body.radius-0 .nav-link,
body.radius-0 .dropdown-menu,
body.radius-0 .choose-skin li div,
body.radius-0 .choose-skin li.active:after,
body.radius-0 .avatar,
body.radius-0 .rounded, body.radius-0 .rounded-1, body.radius-0 .rounded-2, body.radius-0 .rounded-3, body.radius-0 .rounded-4, body.radius-0 .rounded-pill,
body.radius-0 .card .card-img-top,
body.radius-0 .alert,
body.radius-0 .badge,
body.radius-0 .input-group-text {
  border-radius: 0 !important;
}
body.monochrome .sidebar,
body.monochrome .page {
  filter: gray;
  /* IE6-9 */
  -webkit-filter: grayscale(1);
  /* Google Chrome, Safari 6+ & Opera 15+ */
  filter: grayscale(1);
  /* Microsoft Edge and Firefox 35+ */
}

:is(a) {
  color: var(--primary-color);
}
:is(a):hover, :is(a):focus {
  color: var(--accent-color);
  outline: none;
  box-shadow: none;
}

img:hover {
  filter: saturate(0.5);
}

.bullet-dot {
  position: absolute;
  animation: blink 1s linear infinite;
  height: 8px;
  width: 8px;
  border-radius: 10px;
  right: 10px;
  top: -5px;
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.notification a {
  color: var(--gray-6);
  text-decoration: none;
}
.notification a p {
  color: var(--gray-8);
  font-size: 15px;
}
.notification a small, .notification a .small {
  color: var(--gray-5);
  font-size: 10px;
}

.choose-skin {
  display: flex;
  flex-direction: column;
}
.choose-skin li {
  color: var(--gray-5);
}
.choose-skin li > span {
  flex-grow: 1;
}
.choose-skin li div {
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  display: inline-block;
  overflow: hidden;
  position: relative;
  padding: 4px 8px;
  font-size: 11px;
  color: var(--white);
}
.choose-skin li.active, .choose-skin li:hover {
  color: var(--primary-color);
}

.pricing-table .owl-stage {
  display: flex;
  align-items: stretch;
}
.pricing-table .owl-stage .owl-item {
  background: var(--card-color);
  border-radius: 0.75rem;
}
.pricing-table .owl-stage .owl-item .item {
  display: flex;
  flex-direction: column;
  height: 100%;
}

ul.row {
  list-style: none;
}

.row-deck > .col,
.row-deck > [class*=col-] {
  display: flex;
  align-items: stretch;
}
.row-deck > .col .card,
.row-deck > [class*=col-] .card {
  flex: 1 1 auto;
}
.row-deck .row-deck {
  flex: 1 0 0%;
}

.row-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media only screen and (max-width: 768px) {
  .row-title {
    flex-direction: column;
    text-align: center;
  }
  .row-title > div {
    margin-top: 10px;
  }
}

.contacts > li {
  background: var(--card-color);
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  padding: 1rem 1.5rem;
  border-radius: 0.7rem;
  margin-bottom: 5px;
}
.contacts > li:hover {
  border-color: var(--primary-color);
}
.contacts .form-check {
  margin-right: 1rem;
}
.contacts a {
  display: flex;
  text-decoration: none;
}

.lightGallery .light-link {
  background: var(--primary-gradient);
  display: block;
}
.lightGallery img {
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  filter: grayscale(1);
  opacity: 0.7;
}
.lightGallery img:hover {
  opacity: 1;
  filter: none;
}

.inline-calendar .datepicker-inline,
.inline-calendar .table-condensed {
  width: 100% !important;
  padding: 0;
}
.inline-calendar .day {
  height: 40px;
  width: 30px;
  font-size: 12px;
}
.inline-calendar .day.today {
  background: var(--primary-color) !important;
  color: var(--white) !important;
}
.inline-calendar .day.active {
  background: var(--accent-color) !important;
  color: var(--white) !important;
}

.chips > a {
  border: 1px solid var(--border-color);
  border-radius: 30px;
  padding-right: 15px;
  align-items: center;
  vertical-align: bottom;
}

.tech-icon ul {
  padding-left: 0px !important;
}
.tech-icon ul li {
  min-width: 40px;
  min-height: 40px;
  background: var(--white);
  border-radius: 50%;
  margin-right: 15px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tech-icon ul li::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  border: 1px solid var(--white);
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-animation: play-btn-2 1.5s linear infinite;
  animation: play-btn-2 1.5s linear infinite;
}

.responsive-tabs-container .responsive-tabs li {
  border-right: 1px solid var(--border-color);
}
.responsive-tabs-container .responsive-tabs li:last-child {
  border: 0;
}
.responsive-tabs-container .responsive-tabs li.active > a, .responsive-tabs-container .responsive-tabs li:hover > a {
  color: var(--gray-8);
  color: var(--primary-color);
