.submit-track{
    position: relative;
    overflow: hidden;
    margin: 20px 0;
    padding: 0 20px 0 90px;

    li {
        position: relative;
        padding: 0 0 30px 20px;

        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 1px;
            height: 100%;
            background-color: var(--primary-color);
        }
    }
    >li:last-child {
        padding-bottom: 0;
    }
    .date {
        background-color: var(--border-color);
        color: var(--gray-7);
        position: absolute;
        margin-right: 20px;
        top: 0;
        right: 100%;
    }
    .circle {
        background-color: var(--primary-color);
        color: $white;
        position: absolute;
        text-align: center;
        font-size: 13px;
        left: 0;
        top: 0;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        transform: translateX(-50%);
        z-index: 1;
    }
    ul {
        margin: 10px 0 -30px -20px;
        li .circle {
            top: 6px;
            width: 8px;
            height: 8px;
        }
    }
}

.avatar-list{
    img{
        border: 2px solid var(--card-color);
        margin-left: -18px;
        &:hover{
            box-shadow: 0 0.5rem 1rem rgba($dark, .1) !important;
            position: relative;
            z-index: 99;
        }
    }
}
.team-members{
    img{
        border: 2px solid var(--card-color);
        margin-left: -10px;

        &:hover{
            box-shadow: 0 0.5rem 1rem rgba($dark, .1) !important;
            position: relative;
            z-index: 99;
        }
    }
}

.list-style{
    > li{
        > a{
            color: var(--gray-7);
            text-decoration: none;
            font-size: 1rem;
        }
        >ol, ul{
            border-left: 1px solid var(--border-color);
            margin-left: 7px;
            a{
                color: var(--gray-6);
                text-decoration: none;
                font-size: 14px;
            }
        }
    }
    .bullet{
        height: 5px;
        width: .6rem;
        vertical-align: middle;
        display: inline-flex;
    }
}
.list-style-1{
    list-style: none;
    a{
        text-decoration: none;
    }
    >li{
        position: relative;
        &::after{
            background: var(--primary-color);
            content: "";
            position: absolute;
            left: -1.5rem;
            height: 6px;
            width: 16px;
            top: 13px;
        }
        a{
            color: var(--gray-8);
            text-decoration: none;
            font-size: 16px;
        }
        ul{
            > li{
                a{
                    font-size: 15px;
                    color: var(--gray-7);
                }
            }
        }
    }
}

.w-transform{
    display: flex;
    > li{
        width: 60px;
        span{
            transform: rotate(-90deg);
        }
    }
}