.noUi-target{
    background: var(--gray-2);
    border-color: var(--border-color);
    box-shadow: none;
    
    .noUi-connect{
        background: var(--primary-color);
    }
    .noUi-handle{
        width: 28px;
        border-radius: 30px;
        
        &:before{
            left: 10px;
        }
        &:after {
            left: 15px;
        }
    }
}