// Bootstrap TouchSpin
.bootstrap-touchspin{
    border: 1px solid var(--border-color);
    padding: 4px;
    border-radius: 5px;
    display: flex;
    align-items: center;

    .form-control{
        border: 0;
        margin-left: 0 !important;
        margin-right: 0 !important;

        &:focus,
        &:focus-within,
        &:focus-visible,
        &:active,
        &:hover{
            box-shadow: none;
            border: 0;
            outline: none;
        }
    }
    .input-group-addon{
        margin: 0 2px !important;
        padding: 0;
    }
    .input-group-text{
        height: 35px;
    }
}
